:root{
  --fb-text: #0f172a;
  --fb-muted: #64748b;
  --fb-border: rgba(15,23,42,0.10);
  --fb-shadow: 0 18px 55px rgba(15,23,42,0.08);
  --fb-radius: 22px;
  --fb-radius2: 14px;

  --fb-blue: #2563eb;
  --fb-blueSoft: rgba(37,99,235,0.10);
  --fb-focus: 0 0 0 4px rgba(37,99,235,0.12);
}

.fbWrap{
  margin-top: 12px;
  border: 1px solid var(--fb-border);
  background: rgba(255,255,255,0.96);
  border-radius: var(--fb-radius);
  box-shadow: var(--fb-shadow);
  overflow: hidden;
}

/* header */
.fbHead{
  padding: 12px 14px;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  gap: 12px;
  border-bottom: 1px solid rgba(15,23,42,0.08);
}
.fbHeadLeft{ min-width: 0; }
.fbTitle{ font-weight: 950; color: var(--fb-text); letter-spacing: -0.01em; }
.fbSub{ color: var(--fb-muted); font-size: 13px; margin-top: 3px; }

.fbDatePill{
  flex: 0 0 auto;
  height: 34px;
  padding: 0 12px;
  border-radius: 999px;
  border: 1px solid rgba(15,23,42,0.10);
  background: rgba(15,23,42,0.03);
  color: var(--fb-text);
  font-weight: 900;
  display: inline-flex;
  align-items: center;
  gap: 8px;
  white-space: nowrap;
}

.fbBody{ padding: 14px; }

/* ✅ MAIN GRID - arranged & consistent */
.fbGrid{
  display: grid;
  gap: 12px;
  align-items: end;

  /* Desktop: 4 columns, 2 rows */
  grid-template-columns: repeat(4, minmax(0, 1fr));
  grid-template-areas:
    "from to type receiver"
    "search search search actions";
}

/* Areas */
.fbFrom{ grid-area: from; }
.fbTo{ grid-area: to; }
.fbType{ grid-area: type; }
.fbReceiver{ grid-area: receiver; }
.fbSearch{ grid-area: search; }
.fbActionsCell{ grid-area: actions; }

/* field */
.fbField{ min-width: 0; }
.fbField label{
  display: block;
  font-size: 12px;
  font-weight: 900;
  color: var(--fb-muted);
  margin-bottom: 6px;
}

/* control */
.fbControl{
  width: 100%;
  min-width: 0;
  height: 44px;
  border-radius: var(--fb-radius2);
  border: 1px solid rgba(15,23,42,0.12);
  outline: none;
  padding: 0 12px;
  font-weight: 800;
  color: var(--fb-text);
  background: #fff;
  box-sizing: border-box;
}

.fbControl::placeholder{
  color: rgba(100,116,139,0.8);
  font-weight: 700;
}

.fbControl:focus{
  border-color: rgba(37,99,235,0.35);
  box-shadow: var(--fb-focus);
}

/* ✅ Buttons aligned inside grid */
.fbActions{
  display: flex;
  gap: 10px;
  justify-content: flex-end;
  align-items: center;
  height: 44px; /* aligns with inputs */
}

.fbBtn{
  height: 44px;
  padding: 0 16px;
  border-radius: 14px;
  border: 1px solid rgba(15,23,42,0.12);
  background: rgba(15,23,42,0.04);
  color: var(--fb-text);
  font-weight: 950;
  cursor: pointer;
  transition: transform .15s ease, background .15s ease;
  white-space: nowrap;
}
.fbBtn:hover{ transform: translateY(-1px); background: rgba(15,23,42,0.06); }

.fbBtnPrimary{
  border-color: rgba(37, 99, 235, 0.35);
  background: var(--fb-blueSoft);
  color: #0b1b4a;
}
.fbBtnPrimary:hover{ background: rgba(37,99,235,0.14); }

/* ✅ Tablet: 2 columns arrangement */
@media (max-width: 1050px){
  .fbGrid{
    grid-template-columns: repeat(2, minmax(0, 1fr));
    grid-template-areas:
      "from to"
      "type receiver"
      "search search"
      "actions actions";
  }

  .fbActions{
    justify-content: flex-end;
  }
}

/* ✅ Mobile: stacked + full width buttons */
@media (max-width: 640px){
  .fbHead{
    flex-direction: column;
    align-items: flex-start;
  }

  .fbGrid{
    grid-template-columns: 1fr;
    grid-template-areas:
      "from"
      "to"
      "type"
      "receiver"
      "search"
      "actions";
  }

  .fbActions{
    display: grid;
    grid-template-columns: 1fr 1fr;
    height: auto;
  }

  .fbBtn{
    width: 100%;
  }
}

@media (max-width: 420px){
  .fbActions{
    grid-template-columns: 1fr;
  }
}
