:root{
  --p-bg: #f6f7fb;
  --p-card: #ffffff;
  --p-text: #0f172a;
  --p-muted: #64748b;
  --p-border: rgba(15, 23, 42, 0.10);
  --p-shadow: 0 18px 50px rgba(15, 23, 42, 0.08);
  --p-shadow2: 0 14px 34px rgba(15, 23, 42, 0.10);
  --p-radius: 18px;
  --p-radius2: 14px;

  --p-blue: #2563eb;
  --p-green: #16a34a;
  --p-red: #ef4444;
  --p-chip: rgba(37, 99, 235, 0.10);
}

.projectsPage{
  width: 100%;
}

.pHero{
  border: 1px solid var(--p-border);
  background: linear-gradient(135deg, rgba(37,99,235,0.08), rgba(99,102,241,0.06), rgba(16,185,129,0.06));
  border-radius: 22px;
  box-shadow: var(--p-shadow);
  padding: 18px;
  overflow: hidden;
}

.pHeroTop{
  display: grid;
  grid-template-columns: 1fr auto;
  gap: 14px;
  align-items: center;
}

.pTitle{
  font-size: clamp(20px, 2.4vw, 28px);
  font-weight: 950;
  letter-spacing: -0.02em;
  color: var(--p-text);
  line-height: 1.15;
}

.pSub{
  color: var(--p-muted);
  margin-top: 6px;
  font-size: 14px;
}

.pHeroActions{
  display: flex;
  gap: 10px;
  align-items: center;
}

.pPrimaryBtn{
  height: 42px;
  padding: 0 14px;
  border-radius: 14px;
  border: 1px solid rgba(37, 99, 235, 0.35);
  background: var(--p-blue);
  color: white;
  font-weight: 900;
  display: inline-flex;
  align-items: center;
  gap: 10px;
  cursor: pointer;
  box-shadow: 0 18px 46px rgba(37, 99, 235, 0.25);
  transition: transform .15s ease, filter .15s ease;
}
.pPrimaryBtn:hover{ transform: translateY(-1px); filter: brightness(1.02); }

.pGhostBtn{
  height: 42px;
  padding: 0 12px;
  border-radius: 14px;
  border: 1px solid rgba(15, 23, 42, 0.12);
  background: rgba(255,255,255,0.9);
  color: var(--p-text);
  font-weight: 900;
  display: inline-flex;
  align-items: center;
  gap: 10px;
  cursor: pointer;
  transition: transform .15s ease;
}
.pGhostBtn:hover{ transform: translateY(-1px); }

.pStats{
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-top: 14px;
}

.pPill{
  display: inline-flex;
  align-items: center;
  gap: 10px;
  padding: 9px 12px;
  border-radius: 999px;
  border: 1px solid rgba(15, 23, 42, 0.10);
  background: rgba(255,255,255,0.85);
  font-weight: 900;
  color: var(--p-text);
  font-size: 12px;
}

.pPill--active{
  border-color: rgba(22, 163, 74, 0.22);
  background: rgba(22, 163, 74, 0.10);
  color: #0b3b1b;
}

.pDivider{
  height: 14px;
}

.pGrid{
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  gap: 12px;
}

.pCard{
  border: 1px solid rgba(15, 23, 42, 0.10);
  background: var(--p-card);
  border-radius: var(--p-radius);
  box-shadow: var(--p-shadow2);
  overflow: hidden;
  cursor: pointer;
  transition: transform .15s ease, box-shadow .15s ease, border-color .15s ease;
  min-width: 0;
}
.pCard:hover{
  transform: translateY(-2px);
  border-color: rgba(37, 99, 235, 0.22);
  box-shadow: 0 22px 60px rgba(15, 23, 42, 0.12);
}

.pCardBody{
  padding: 14px;
}

.pCardTop{
  display: grid;
  grid-template-columns: 44px 1fr auto;
  gap: 12px;
  align-items: center;
  min-width: 0;
}

.pBadge{
  width: 44px;
  height: 44px;
  border-radius: 16px;
  display: grid;
  place-items: center;
  background: linear-gradient(135deg, rgba(37,99,235,0.92), rgba(99,102,241,0.82));
  color: white;
  font-weight: 950;
  letter-spacing: -0.01em;
}

.pName{
  font-weight: 950;
  color: var(--p-text);
  letter-spacing: -0.01em;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
}

.pDesc{
  color: var(--p-muted);
  font-size: 13px;
  margin-top: 4px;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.pStatus{
  height: 28px;
  padding: 0 10px;
  border-radius: 999px;
  font-size: 12px;
  font-weight: 950;
  display: inline-flex;
  align-items: center;
  border: 1px solid rgba(15, 23, 42, 0.10);
  background: rgba(15, 23, 42, 0.04);
  color: var(--p-text);
  white-space: nowrap;
}
.pStatus--active{
  border-color: rgba(22, 163, 74, 0.22);
  background: rgba(22, 163, 74, 0.10);
  color: #0b3b1b;
}

.pMetaRow{
  margin-top: 12px;
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.pChip{
  border: 1px solid rgba(15, 23, 42, 0.10);
  background: rgba(37, 99, 235, 0.08);
  color: #0b1b4a;
  padding: 7px 10px;
  border-radius: 999px;
  font-size: 12px;
  font-weight: 900;
}

.pActions{
  margin-top: 12px;
  display: grid;
  grid-template-columns: 1fr 44px;
  gap: 10px;
}

.pOpenBtn{
  height: 40px;
  border-radius: 14px;
  border: 1px solid rgba(37, 99, 235, 0.25);
  background: rgba(37, 99, 235, 0.12);
  color: #0b1b4a;
  font-weight: 950;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  cursor: pointer;
  transition: transform .15s ease, background .15s ease;
}
.pOpenBtn:hover{ transform: translateY(-1px); background: rgba(37, 99, 235, 0.16); }

.pDelBtn{
  width: 44px;
  height: 40px;
  border-radius: 14px;
  border: 1px solid rgba(239, 68, 68, 0.22);
  background: rgba(239, 68, 68, 0.10);
  color: #7f1d1d;
  cursor: pointer;
  transition: transform .15s ease, background .15s ease;
  display: grid;
  place-items: center;
}
.pDelBtn:hover{ transform: translateY(-1px); background: rgba(239, 68, 68, 0.14); }

.pEmpty{
  border: 1px dashed rgba(15, 23, 42, 0.18);
  background: rgba(255,255,255,0.9);
  border-radius: 22px;
  box-shadow: var(--p-shadow);
  padding: 18px;
}
.pEmpty h3{
  margin: 0;
  font-size: 16px;
  font-weight: 950;
  color: var(--p-text);
}
.pEmpty p{
  margin: 8px 0 0;
  color: var(--p-muted);
  font-size: 14px;
}

/* Loading / Error */
.pCenter{
  display: grid;
  place-items: center;
  padding: 22px 0;
}
.pError{
  color: #b91c1c;
  font-weight: 900;
  padding: 12px 0;
}

/* Responsive */
@media (max-width: 980px){
  .pGrid{ grid-template-columns: repeat(2, minmax(0, 1fr)); }
}
@media (max-width: 560px){
  .pHeroTop{ grid-template-columns: 1fr; }
  .pHeroActions{ width: 100%; }
  .pPrimaryBtn{ width: 100%; justify-content: center; }
  .pGhostBtn{ width: 100%; justify-content: center; }

  .pGrid{ grid-template-columns: 1fr; }

  .pCardTop{
    grid-template-columns: 44px 1fr;
    grid-template-rows: auto auto;
  }
  .pCardTop > :last-child{
    grid-column: 1 / -1;
    justify-self: start;
  }
}
