.desktop {
  padding: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  background: linear-gradient(
    135deg,
    rgba(37, 99, 235, 0.08),
    rgba(99, 102, 241, 0.06),
    rgba(16, 185, 129, 0.06)
  );
  margin-bottom: 1rem;
}

.desk_link {
  cursor: pointer;
  text-transform: capitalize;
  font-weight: 500;
}
.desk_link:hover {
  color: gray;
}
