:root {
  --t-bg: #f6f7fb;
  --t-text: #0f172a;
  --t-muted: #64748b;
  --t-muted2: #94a3b8;
  --t-border: rgba(15, 23, 42, 0.1);
  --t-shadow: 0 18px 55px rgba(15, 23, 42, 0.08);
  --t-shadow2: 0 14px 36px rgba(15, 23, 42, 0.1);
  --t-radius: 20px;
  --t-radius2: 14px;

  --t-blue: #2563eb;
  --t-green: #16a34a;
  --t-orange: #f59e0b;
  --t-red: #ef4444;
}

.txPage {
  width: 100%;
}

.txHero {
  border: 1px solid var(--t-border);
  background: linear-gradient(
    135deg,
    rgba(37, 99, 235, 0.1),
    rgba(99, 102, 241, 0.08),
    rgba(16, 185, 129, 0.06)
  );
  border-radius: 22px;
  box-shadow: var(--t-shadow);
  padding: 18px;
}

.txHeroTop {
  display: grid;
  grid-template-columns: 1fr auto;
  gap: 14px;
  align-items: center;
}

.txTitle {
  font-size: clamp(20px, 2.4vw, 28px);
  font-weight: 950;
  letter-spacing: -0.02em;
  color: var(--t-text);
  line-height: 1.15;
}

.txSub {
  color: var(--t-muted);
  margin-top: 6px;
  font-size: 14px;
}

.txHeroActions {
  display: flex;
  gap: 10px;
  align-items: center;
}

.txPrimaryBtn {
  height: 42px;
  padding: 0 14px;
  border-radius: 14px;
  border: 1px solid rgba(37, 99, 235, 0.35);
  background: var(--t-blue);
  color: #fff;
  font-weight: 950;
  display: inline-flex;
  align-items: center;
  gap: 10px;
  cursor: pointer;
  box-shadow: 0 18px 46px rgba(37, 99, 235, 0.22);
  transition:
    transform 0.15s ease,
    filter 0.15s ease;
}
.txPrimaryBtn:hover {
  transform: translateY(-1px);
  filter: brightness(1.02);
}

.txGhostBtn {
  height: 42px;
  padding: 0 12px;
  border-radius: 14px;
  border: 1px solid rgba(15, 23, 42, 0.12);
  background: rgba(255, 255, 255, 0.88);
  color: var(--t-text);
  font-weight: 950;
  display: inline-flex;
  align-items: center;
  gap: 10px;
  cursor: pointer;
  transition: transform 0.15s ease;
}
.txGhostBtn:hover {
  transform: translateY(-1px);
}

.txPills {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-top: 12px;
}

.txPill {
  display: inline-flex;
  align-items: center;
  gap: 10px;
  padding: 9px 12px;
  border-radius: 999px;
  border: 1px solid rgba(15, 23, 42, 0.1);
  background: rgba(255, 255, 255, 0.88);
  color: var(--t-text);
  font-weight: 950;
  font-size: 12px;
}

/* Filters panel */
.filtersPanel {
  margin-top: 12px;
  border: 1px solid rgba(15, 23, 42, 0.1);
  background: rgba(255, 255, 255, 0.95);
  border-radius: 22px;
  box-shadow: var(--t-shadow);
  overflow: hidden;
}

.filtersHead {
  padding: 12px 14px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 12px;
  border-bottom: 1px solid rgba(15, 23, 42, 0.08);
}

.filtersHeadTitle {
  font-weight: 950;
  color: var(--t-text);
  letter-spacing: -0.01em;
}
.filtersHeadDesc {
  color: var(--t-muted);
  font-size: 13px;
  margin-top: 2px;
}

.filtersBody {
  padding: 14px;
}

.filtersGrid {
  display: grid;
  grid-template-columns: 1.1fr 1.1fr 1.4fr 0.7fr 1fr auto;
  gap: 10px;
  align-items: end;
}

.fField label {
  display: block;
  font-size: 12px;
  font-weight: 900;
  color: var(--t-muted);
  margin-bottom: 6px;
}

.fInput,
.fSelect {
  width: 100%;
  height: 42px;
  border-radius: 14px;
  border: 1px solid rgba(15, 23, 42, 0.12);
  outline: none;
  padding: 0 12px;
  font-weight: 800;
  color: var(--t-text);
  background: #fff;
}

.fInput:focus,
.fSelect:focus {
  border-color: rgba(37, 99, 235, 0.35);
  box-shadow: 0 0 0 4px rgba(37, 99, 235, 0.12);
}

.fActions {
  display: flex;
  gap: 10px;
  justify-content: flex-end;
  align-items: center;
}

.fApply {
  height: 42px;
  padding: 0 14px;
  border-radius: 14px;
  border: 1px solid rgba(37, 99, 235, 0.35);
  background: rgba(37, 99, 235, 0.1);
  color: #0b1b4a;
  font-weight: 950;
  cursor: pointer;
  transition:
    transform 0.15s ease,
    background 0.15s ease;
}
.fApply:hover {
  transform: translateY(-1px);
  background: rgba(37, 99, 235, 0.14);
}

.fReset {
  height: 42px;
  padding: 0 14px;
  border-radius: 14px;
  border: 1px solid rgba(15, 23, 42, 0.12);
  background: rgba(15, 23, 42, 0.04);
  color: var(--t-text);
  font-weight: 950;
  cursor: pointer;
  transition: transform 0.15s ease;
}
.fReset:hover {
  transform: translateY(-1px);
}

/* Summary section */
.txSummary{
  margin-top: 12px;
  display: grid;
  grid-template-columns: repeat(4, minmax(0, 1fr));
  gap: 12px;
}

.txSummaryCard{
  border: 1px solid rgba(15, 23, 42, 0.10);
  background: rgba(255,255,255,0.98);
  border-radius: 22px;
  box-shadow: var(--t-shadow2);
  padding: 14px;
  min-width: 0;
  position: relative;
  overflow: hidden;
}

.txSummaryCard::before{
  content: "";
  position: absolute;
  inset: 0;
  background: linear-gradient(135deg, rgba(37,99,235,0.10), rgba(99,102,241,0.06), rgba(16,185,129,0.04));
  opacity: 0.7;
  pointer-events: none;
}

.txSummaryLabel{
  position: relative;
  z-index: 1;
  font-size: 12px;
  font-weight: 950;
  color: var(--t-muted);
  letter-spacing: -0.01em;
}

.txSummaryValue{
  position: relative;
  z-index: 1;
  margin-top: 8px;
  font-size: clamp(18px, 2vw, 22px);
  font-weight: 950;
  color: var(--t-text);
  letter-spacing: -0.02em;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.txSummaryHint{
  position: relative;
  z-index: 1;
  margin-top: 6px;
  font-size: 12px;
  color: var(--t-muted2);
  font-weight: 800;
}

.txSummaryCard--warn::before{
  background: linear-gradient(135deg, rgba(245,158,11,0.14), rgba(239,68,68,0.08));
}

/* Responsive summary */
@media (max-width: 1100px){
  .txSummary{ grid-template-columns: repeat(2, minmax(0, 1fr)); }
}
@media (max-width: 720px){
  .txSummary{ grid-template-columns: 1fr; }
}





/* Cards grid */
.txGrid {
  margin-top: 12px;
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  gap: 12px;
}

/* Transaction slip card */
.txCard {
  border: 1px solid rgba(15, 23, 42, 0.1);
  background: rgba(255, 255, 255, 0.98);
  border-radius: 22px;
  box-shadow: var(--t-shadow2);
  overflow: hidden;
  transition:
    transform 0.15s ease,
    box-shadow 0.15s ease,
    border-color 0.15s ease;
  min-width: 0;
}
.txCard:hover {
  transform: translateY(-2px);
  border-color: rgba(37, 99, 235, 0.22);
  box-shadow: 0 22px 60px rgba(15, 23, 42, 0.14);
}

.txCardBody {
  padding: 14px;
}

.txTop {
  display: grid;
  grid-template-columns: 44px 1fr auto;
  gap: 12px;
  align-items: start;
  min-width: 0;
}

.txBadge {
  width: 44px;
  height: 44px;
  border-radius: 16px;
  display: grid;
  place-items: center;
  color: #fff;
  font-weight: 950;
  letter-spacing: -0.01em;
  background: linear-gradient(
    135deg,
    rgba(37, 99, 235, 0.92),
    rgba(99, 102, 241, 0.82)
  );
}

.txTitle2 {
  font-weight: 950;
  color: var(--t-text);
  letter-spacing: -0.01em;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.txReceiver {
  color: var(--t-muted);
  font-size: 13px;
  margin-top: 3px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

/* ✅ Meta pills under receiver */
.txMetaRow {
  margin-top: 8px;
  display: flex;
  /* gap: 8px; */
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;

}

.txMetaPill {
  display: inline-flex;
  align-items: center;
  gap: 6px;
  height: 28px;
  padding: 0 10px;
  border-radius: 999px;
  font-size: 12px;
  font-weight: 950;
  border: 1px solid rgba(15, 23, 42, 0.1);
  background: rgba(15, 23, 42, 0.04);
  color: var(--t-text);
  white-space: nowrap;
}

.txMetaPill--qty {
  border-color: rgba(99, 102, 241, 0.22);
  background: rgba(99, 102, 241, 0.1);
  color: #1d1b4a;
}



.txType {
  height: 28px;
  padding: 0 10px;
  border-radius: 999px;
  font-size: 12px;
  font-weight: 950;
  display: inline-flex;
  align-items: center;
  border: 1px solid rgba(15, 23, 42, 0.1);
  background: rgba(15, 23, 42, 0.04);
  color: var(--t-text);
  white-space: nowrap;
}
.txType--full {
  border-color: rgba(22, 163, 74, 0.22);
  background: rgba(22, 163, 74, 0.1);
  color: #0b3b1b;
}
.txType--partial {
  border-color: rgba(245, 158, 11, 0.25);
  background: rgba(245, 158, 11, 0.12);
  color: #4a2a00;
}

.txMid {
  margin-top: 12px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 12px;
  padding: 12px;
  border-radius: 18px;
  border: 1px solid rgba(15, 23, 42, 0.08);
  background: rgba(15, 23, 42, 0.03);
}

.kv small {
  display: block;
  color: var(--t-muted2);
  font-weight: 900;
  font-size: 11px;
}
.kv b {
  display: block;
  color: var(--t-text);
  font-weight: 950;
  margin-top: 4px;
}

.txMid3 {
  margin-top: 10px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 12px;
}

.noteRow {
  margin-top: 12px;
  display: flex;
  align-items: center;
  gap: 10px;
  color: var(--t-muted);
  font-size: 12px;
  font-weight: 800;
  min-width: 0;
}

.noteText {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
}

.txFooter {
  margin-top: 12px;
  display: flex;
  justify-content: space-between;
  gap: 10px;
  align-items: center;
}

.txActions {
  display: flex;
  gap: 8px;
}

.iconBtn {
  width: 40px;
  height: 40px;
  border-radius: 14px;
  border: 1px solid rgba(15, 23, 42, 0.12);
  background: rgba(15, 23, 42, 0.03);
  cursor: pointer;
  display: grid;
  place-items: center;
  transition:
    transform 0.15s ease,
    background 0.15s ease;
}
.iconBtn:hover {
  transform: translateY(-1px);
  background: rgba(15, 23, 42, 0.06);
}

.iconBtn--danger {
  border-color: rgba(239, 68, 68, 0.22);
  background: rgba(239, 68, 68, 0.1);
  color: #7f1d1d;
}
.iconBtn--danger:hover {
  background: rgba(239, 68, 68, 0.14);
}

.txOpenBtn {
  height: 40px;
  padding: 0 12px;
  border-radius: 14px;
  border: 1px solid rgba(37, 99, 235, 0.25);
  background: rgba(37, 99, 235, 0.1);
  color: #0b1b4a;
  font-weight: 950;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  gap: 8px;
  transition:
    transform 0.15s ease,
    background 0.15s ease;
}
.txOpenBtn:hover {
  transform: translateY(-1px);
  background: rgba(37, 99, 235, 0.14);
}

/* Pagination */
.pagi {
  margin-top: 16px;
  display: flex;
  justify-content: center;
  gap: 10px;
  flex-wrap: wrap;
}
.pagiBtn {
  height: 40px;
  padding: 0 14px;
  border-radius: 14px;
  border: 1px solid rgba(15, 23, 42, 0.12);
  background: rgba(255, 255, 255, 0.9);
  color: var(--t-text);
  font-weight: 950;
  cursor: pointer;
}
.pagiBtn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}
.pagiText {
  height: 40px;
  display: inline-flex;
  align-items: center;
  padding: 0 14px;
  border-radius: 14px;
  border: 1px solid rgba(15, 23, 42, 0.1);
  background: rgba(255, 255, 255, 0.7);
  color: var(--t-muted);
  font-weight: 900;
}

/* Responsive */
@media (max-width: 1100px) {
  .txGrid {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
  .filtersGrid {
    grid-template-columns: 1fr 1fr 1fr 0.8fr 1fr;
  }
  .filtersGrid .fActions {
    grid-column: 1 / -1;
    justify-content: flex-end;
  }
}
@media (max-width: 720px) {
  .txHeroTop {
    grid-template-columns: 1fr;
  }
  .txHeroActions {
    width: 100%;
  }
  .txPrimaryBtn,
  .txGhostBtn {
    width: 100%;
    justify-content: center;
  }

  .filtersGrid {
    grid-template-columns: 1fr;
  }
  .filtersGrid .fActions {
    grid-column: auto;
    justify-content: stretch;
  }
  .fActions {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }

  .txGrid {
    grid-template-columns: 1fr;
  }

  .txTop {
    grid-template-columns: 44px 1fr;
    grid-template-rows: auto auto;
  }
  .txTop > :last-child {
    grid-column: 1 / -1;
    justify-self: start;
  }
}
/* ✅ Make sure pills never break the card on tiny screens */
@media (max-width: 720px) {
  .txMetaRow {
    gap: 6px;
  }
  .txMetaPill {
    height: 26px;
    padding: 0 9px;
  }
}