:root {
  --bg: #0b1020;
  --panel: rgba(255, 255, 255, 0.06);
  --card: #0f172a;
  --card2: rgba(255,255,255,0.07);
  --stroke: rgba(255, 255, 255, 0.10);
  --text: rgba(255, 255, 255, 0.92);
  --muted: rgba(255, 255, 255, 0.66);
  --muted2: rgba(255, 255, 255, 0.50);
  --shadow: 0 22px 60px rgba(0,0,0,0.35);
  --shadow2: 0 16px 40px rgba(0,0,0,0.25);
  --radius: 18px;
  --radius2: 14px;
  --accent: #6366f1;
  --accent2: #10b981;
  --accent3: #f472b6;
}

.db {
  min-height: calc(100vh - 64px);
  padding: 18px 0 26px;
  background: var(--bg);
  background-image:
    radial-gradient(900px 400px at 10% 10%, rgba(99,102,241,0.35), transparent 60%),
    radial-gradient(800px 380px at 90% 20%, rgba(16,185,129,0.25), transparent 60%),
    radial-gradient(900px 480px at 60% 110%, rgba(244,114,182,0.18), transparent 60%);
}

.db__wrap {
  width: min(1120px, calc(100% - 28px));
  margin: 0 auto;
}

.db__hero {
  border: 1px solid var(--stroke);
  background: rgba(255,255,255,0.05);
  backdrop-filter: blur(12px);
  border-radius: 22px;
  box-shadow: var(--shadow);
  padding: 18px;
  overflow: hidden;
}

.db__heroTop {
  display: grid;
  grid-template-columns: 1fr auto;
  gap: 14px;
  align-items: center;
}

.db__title {
  font-size: clamp(22px, 3vw, 34px);
  line-height: 1.1;
  font-weight: 900;
  letter-spacing: -0.03em;
  color: var(--text);
}

.db__subtitle {
  color: var(--muted);
  margin-top: 6px;
  font-size: 14px;
}

.db__chips {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-top: 14px;
}

.db__chip {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 9px 12px;
  border-radius: 999px;
  border: 1px solid rgba(255,255,255,0.12);
  background: rgba(255,255,255,0.06);
  color: var(--text);
  font-weight: 800;
  font-size: 12px;
}

.db__actions {
  display: flex;
  gap: 10px;
  align-items: center;
}

.db__iconBtn {
  height: 42px;
  width: 42px;
  border-radius: 14px;
  border: 1px solid rgba(255,255,255,0.14);
  background: rgba(255,255,255,0.06);
  display: grid;
  place-items: center;
  color: var(--text);
  cursor: pointer;
  transition: transform .15s ease, background .15s ease;
}
.db__iconBtn:hover { transform: translateY(-1px); background: rgba(255,255,255,0.09); }

.db__primaryBtn {
  height: 42px;
  padding: 0 14px;
  border-radius: 14px;
  border: 1px solid rgba(99,102,241,0.55);
  background: rgba(99,102,241,0.92);
  color: white;
  font-weight: 900;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  gap: 10px;
  box-shadow: 0 18px 44px rgba(99,102,241,0.35);
  transition: transform .15s ease;
}
.db__primaryBtn:hover { transform: translateY(-1px); }

.db__sectionGap { height: 16px; }

.db__kpis {
  display: grid;
  grid-template-columns: repeat(4, minmax(0, 1fr));
  gap: 12px;
  margin-top: 14px;
}

.kpi {
  border: 1px solid var(--stroke);
  background: rgba(255,255,255,0.06);
  border-radius: var(--radius);
  box-shadow: var(--shadow2);
  padding: 14px;
  display: grid;
  grid-template-columns: 44px 1fr;
  gap: 12px;
  align-items: center;
}

.kpi__icon {
  width: 44px;
  height: 44px;
  border-radius: 16px;
  display: grid;
  place-items: center;
  background: rgba(255,255,255,0.10);
  border: 1px solid rgba(255,255,255,0.12);
  color: white;
}

.kpi__label { color: var(--muted); font-size: 12px; font-weight: 800; }
.kpi__value { color: var(--text); font-size: 20px; font-weight: 950; letter-spacing: -0.02em; margin-top: 2px; }
.kpi__hint { color: var(--muted2); font-size: 12px; margin-top: 2px; white-space: nowrap; overflow: hidden; text-overflow: ellipsis; }

.db__grid {
  display: grid;
  grid-template-columns: 1.7fr 1fr;
  gap: 12px;
}

.panel {
  border: 1px solid var(--stroke);
  background: rgba(255,255,255,0.06);
  border-radius: 22px;
  box-shadow: var(--shadow2);
  overflow: hidden;
}

.panel__head {
  padding: 16px 16px 10px;
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  gap: 12px;
}

.panel__title {
  color: var(--text);
  font-weight: 950;
  font-size: 16px;
  letter-spacing: -0.01em;
}

.panel__desc { color: var(--muted); font-size: 13px; margin-top: 4px; }

.panel__headRight {
  display: flex;
  align-items: center;
  gap: 10px;
}

.panel__ghostBtn {
  height: 36px;
  padding: 0 12px;
  border-radius: 12px;
  border: 1px solid rgba(255,255,255,0.14);
  background: rgba(255,255,255,0.06);
  color: var(--text);
  font-weight: 900;
  cursor: pointer;
  transition: transform .15s ease;
}
.panel__ghostBtn:hover { transform: translateY(-1px); }

.panel__body { padding: 0 16px 16px; }

.cardsGrid {
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  gap: 12px;
}

.projectCard {
  border: 1px solid rgba(255,255,255,0.12);
  background: rgba(15, 23, 42, 0.55);
  border-radius: 18px;
  padding: 14px;
  cursor: pointer;
  transition: transform .15s ease, background .15s ease, border-color .15s ease;
  min-width: 0;
}
.projectCard:hover {
  transform: translateY(-2px);
  background: rgba(15, 23, 42, 0.70);
  border-color: rgba(255,255,255,0.18);
}

.projectCard__top {
  display: grid;
  grid-template-columns: 42px 1fr auto;
  gap: 10px;
  align-items: center;
  min-width: 0;
}

.badge {
  width: 42px;
  height: 42px;
  border-radius: 14px;
  display: grid;
  place-items: center;
  background: linear-gradient(135deg, rgba(99,102,241,0.8), rgba(244,114,182,0.55));
  border: 1px solid rgba(255,255,255,0.12);
  color: white;
  font-weight: 950;
}

.projectCard__name {
  color: var(--text);
  font-weight: 950;
  letter-spacing: -0.01em;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.projectCard__meta { color: var(--muted); font-size: 12px; margin-top: 2px; }

.pillRow {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin-top: 10px;
}

.pill {
  padding: 7px 10px;
  border-radius: 999px;
  font-size: 12px;
  font-weight: 850;
  border: 1px solid rgba(255,255,255,0.12);
  background: rgba(255,255,255,0.06);
  color: var(--text);
}

.metrics {
  margin-top: 12px;
  display: grid;
  gap: 8px;
}

.metric {
  display: flex;
  justify-content: space-between;
  gap: 10px;
  min-width: 0;
}
.metric span:first-child { color: var(--muted); font-size: 12px; }
.metric span:last-child { color: var(--text); font-size: 12px; font-weight: 900; white-space: nowrap; }

.slips {
  display: grid;
  gap: 10px;
}

.slip {
  border: 1px solid rgba(255,255,255,0.12);
  background: rgba(15, 23, 42, 0.55);
  border-radius: 18px;
  padding: 12px;
  cursor: pointer;
  transition: transform .15s ease, background .15s ease, border-color .15s ease;
  min-width: 0;
}
.slip:hover { transform: translateY(-2px); background: rgba(15,23,42,0.70); border-color: rgba(255,255,255,0.18); }

.slip__top {
  display: grid;
  grid-template-columns: 42px 1fr auto;
  gap: 10px;
  align-items: start;
  min-width: 0;
}

.slip__title {
  color: var(--text);
  font-weight: 950;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.slip__sub {
  color: var(--muted);
  font-size: 12px;
  margin-top: 2px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.slip__amount {
  color: var(--text);
  font-weight: 950;
  white-space: nowrap;
}

.slip__metaRow {
  margin-top: 10px;
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.tag {
  padding: 7px 10px;
  border-radius: 999px;
  font-size: 12px;
  font-weight: 850;
  border: 1px solid rgba(255,255,255,0.12);
  background: rgba(255,255,255,0.06);
  color: var(--text);
}

/* Empty */
.empty {
  border: 1px dashed rgba(255,255,255,0.20);
  background: rgba(255,255,255,0.05);
  border-radius: 18px;
  padding: 16px;
  color: var(--text);
}
.empty p { color: var(--muted); margin-top: 6px; font-size: 13px; }
.empty button { margin-top: 12px; }

/* Responsive */
@media (max-width: 980px) {
  .db__kpis { grid-template-columns: repeat(2, minmax(0, 1fr)); }
  .db__grid { grid-template-columns: 1fr; }
  .cardsGrid { grid-template-columns: repeat(2, minmax(0, 1fr)); }
}

@media (max-width: 560px) {
  .db__heroTop { grid-template-columns: 1fr; }
  .db__actions { width: 100%; }
  .db__primaryBtn { width: 100%; justify-content: center; }

  .db__kpis { grid-template-columns: 1fr; }
  .cardsGrid { grid-template-columns: 1fr; }

  .projectCard__top, .slip__top {
    grid-template-columns: 42px 1fr;
    grid-template-rows: auto auto;
  }
  .projectCard__top > :last-child,
  .slip__top > :last-child {
    grid-column: 1 / -1;
    justify-self: start;
  }
}
